<template>
  <div class="logins">
    <div class="d-flex flex-column flex-root">
      <div
          class="login login-1 d-flex flex-column flex-lg-row flex-column-fluid bg-white login-signin-on login-signin-on"
          id="kt_login"
      >
        <!--begin::Aside-->
        <div
            class="login-aside d-flex flex-column flex-row-auto"
            style="background-color: #F2C98A;"
        >
          <div class="d-flex flex-column-auto m-logo flex-column ">
            <a href="#" class="text-center mb-10">
              <img
                  src="../../assets/media/logos/logo-letter-1.png"
                  class="max-h-70px"
                  alt=""
              />
            </a>
            <h3
                class="font-weight-bolder text-center font-size-h4 font-size-h1-lg"
                style="color: #986923;"
            >
              Madaniy meros agentligi
            </h3>
          </div>
          <div
              class="aside-img d-flex flex-row-fluid bgi-no-repeat bgi-position-y-bottom bgi-position-x-center bg_login"
          ></div>
        </div>
        <!--begin::Aside-->
        <!--begin::Content-->
        <div
            class="login-content flex-row-fluid d-flex flex-column form_main position-relative overflow-hidden p-7 mx-auto"
        >
          <div class="d-flex flex-column-fluid flex-center">
            <!--begin::Signin-->
            <div class="login-form login-signin">
              <form
                  class="form"
                  novalidate="novalidate"
                  id="kt_login_signin_form"
                  @submit.prevent="onSubmit()"
              >
                <div class="pb-13 pt-lg-0 pt-5">
                  <h3
                      class="font-weight-bolder text-dark font-size-h4 font-size-h1-lg"
                  >
                    Madaniy meros agentligi
                  </h3>
                </div>
                <div class="form-group">
                  <label class="font-size-h6 font-weight-bolder text-dark"
                  >Email</label
                  >
                  <div
                      id="example-input-group-1"
                      label=""
                      label-for="example-input-1"
                  >
                    <input
                        class="form_input h-auto py-7 px-6 rounded-lg "
                        type="text"
                        name="email"
                        ref="email"
                        v-model="form.email"
                    />
                  </div>
                </div>
                <div class="form-group">
                  <div class="d-flex justify-content-between mt-n5">
                    <label class="font-size-h6 font-weight-bolder text-dark pt-5"
                    >Password</label
                    >
                  </div>
                  <div
                      id="example-input-group-2"
                      label=""
                      label-for="example-input-2"
                  >
                    <input
                        class="form_input h-auto py-7 px-6 rounded-lg"
                        type="password"
                        name="password"
                        ref="password"
                        v-model="form.password"
                        autocomplete="off"
                    />
                  </div>
                </div>
                <div class="pb-lg-0 pb-5">
                  <button
                      ref="kt_login_signin_submit"
                      class="btn btn-primary login_btn"
                  >
                    <b-spinner variant="white" v-if="isSpin"></b-spinner>
                    <span v-if="!isSpin">Kirish</span>
                  </button>
                </div>
              </form>
            </div>
          </div>
        </div>
        <!--end::Content-->
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      form: {
        email: "",
        password: "",
      },

      isSpin:false

    };
  },
 methods:{
   onSubmit(){
     this.isSpin=true
     this.$axiosDefault.post('login/',this.form)
         .then(res=>{
           localStorage.setItem('reg_token',res.data.access)
           this.$router.push('/')

         })
         .catch(()=>{
           this.isSpin=false
           this.$toast("Email yoki parol xato ", {
             timeout: 2000,
             type:"error"
           });
         })

   }
 }
}
</script>
