<script>
import Sidebar from "../../components/Header/Sidebar";
import Navbar from "../../components/Header/Navbar";

export default {
  components: {
    Sidebar,
    Navbar
  },
  data() {
    return{
      proCat:[]
    }
  },
  mounted() {
    this.$axiosDefault.get(`me/`,
        {
          headers: {
            'Authorization': `Bearer ${localStorage.getItem('reg_token')}`,
            'Accept-Language':"oz"
          }
        }
    )
        .then(res=>{
          this.$axiosDefault.get(`organizations/${res.data.organization}/product-categories/`,{
            headers: {
              'Authorization': `Bearer ${localStorage.getItem('reg_token')}`,
              'Accept-Language': 'oz'
            }
          })
              .then(res=>{
                this.proCat=res.data
              })
        })
  },
  methods:{
    routePath(item){    this.$axiosDefault.get(`me/`,
        {
          headers: {
            'Authorization': `Bearer ${localStorage.getItem('reg_token')}`,
            'Accept-Language':"oz"
          }
        }
    )
        .then(res=> {
          this.$router.push(`/organizations/${res.data.id}/product-categories/${item.id}/products/`)
        }
        )
    },
    deletes(item) {
      let isBoss = confirm("You really want to delete?");
      if (isBoss == true) {
        this.$axiosDefault.get(`me/`,
            {
              headers: {
                'Authorization': `Bearer ${localStorage.getItem('reg_token')}`,
                'Accept-Language':"oz"
              }
            }
        )
            .then(res=> {
              this.$axiosDefault.delete(`organizations/${res.data.organization}/product-categories/${item.id}/`,
                  {
                    headers: {
                      'Authorization': `Bearer ${localStorage.getItem('reg_token')}`,
                    }
                  })
                  .then(() => {
                    this.$toast("Ma'lumot o'chirildi", {
                      timeout: 2000,
                      type: "success"
                    });
                    this.$axiosDefault.get(`organizations/${res.data.organization}/product-categories/`,{
                      headers: {
                        'Authorization': `Bearer ${localStorage.getItem('reg_token')}`,
                        'Accept-Language': 'oz'
                      }
                    })
                        .then(res=>{
                          this.proCat=res.data
                        })
                  })
            })
      }
    },
  }

}
</script>
