<template>
  <div class="home">
    <Navbar></Navbar>
    <Sidebar></Sidebar>

    <div class="right_actions">
      <b-container>
        <form @submit.prevent="create()">
          <div class="row">
            <div class="col-xl-8">
              <div class="card p-4 card-accent-primary">
                <va-tabs>
                  <va-tab name="O'zbek">
                    <div class="demoTabContent mt-4">

                      <div class="form_mains">
                        <div class="inputs ">
                          <div>
                            <label>Title</label>
                          </div>
                          <div>
                            <input type="text" v-model="title" class="form-control">
                          </div>
                        </div>


                      </div>
                    </div>
                  </va-tab>
                  <va-tab name="Rus">
                    <div class="demoTabContent mt-4">
                      <div class="demoTabContent ">

                        <div class="form_mains">
                          <div class="inputs ">
                            <div>
                              <label>Title</label>
                            </div>
                            <div>
                              <input type="text" v-model="titleru" class="form-control">
                            </div>
                          </div>


                        </div>
                      </div>
                    </div>
                  </va-tab>
                  <va-tab name="Engish">
                    <div class="demoTabContent mt-4">
                      <div class="demoTabContent ">

                        <div class="form_mains">
                          <div class="inputs ">
                            <div>
                              <label>Title</label>
                            </div>
                            <div>
                              <input type="text" v-model="titleen"  class="form-control">
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>
                  </va-tab>
                </va-tabs>
              </div>
            </div>
            <div class="col-xl-4">

              <div class="card p-4 card-accent-primary">
                <div class="inputs">
                  <input type="checkbox" @input="is_main=!is_main" v-model="is_main">
                  <label class="mx-3">Asosiy sahifaga chiqazish</label>
                </div>
                <div class="create_form mt-4">
                  <img :src="imageUrl[0]" width="100%" height="150px"   alt="">
                  <label class="btn btn-primary my-3" for="cr_image1">Rasm</label>
                  <input type="file" class="form-control d-none"
                         id="cr_image1"
                         @change="trySubmitFile"
                         accept="*"
                         name="datafiles"
                         ref="datafiles"

                  >
                </div>
                <button class="btn btn-primary">
                  <b-spinner variant="white" v-if="isSpin"></b-spinner>
                  <span v-if="!isSpin">Saqlash</span>
                </button>
              </div>
            </div>
          </div>
        </form>
      </b-container>
    </div>
  </div>
</template>
<script>
import Sidebar from "../../components/Header/Sidebar";
import Navbar from "../../components/Header/Navbar";

export default {
  components: {
    Sidebar,
    Navbar
  },
  data() {
    return{
      proCat:[],
      isSpin:false,
      title:'',
      titleru:'',
      titleen:'',
      imageUrl:[],
      myImages:[],
      files_name:[],
      is_main:false
    }
  },
  mounted() {
    this.$axiosDefault.get(`me/`,
        {
          headers: {
            'Authorization': `Bearer ${localStorage.getItem('reg_token')}`,
            'Accept-Language':"oz"
          }
        }
    )
        .then(res=>{
          this.$axiosDefault.get(`organizations/${res.data.organization}/product-categories/${this.$route.params.id}`,{
            headers: {
              'Authorization': `Bearer ${localStorage.getItem('reg_token')}`,
              'Accept-Language': 'oz'
            }
          })
          .then(res=>{
            this.title=res.data.title
            this.is_main=res.data.is_main
            this.imageUrl.push(res.data.image)
          })
          this.$axiosDefault.get(`organizations/${res.data.organization}/product-categories/${this.$route.params.id}`,{
            headers: {
              'Authorization': `Bearer ${localStorage.getItem('reg_token')}`,
              'Accept-Language': 'ru'
            }
          })
              .then(res=>{
                this.title=res.data.title
              })
          this.$axiosDefault.get(`organizations/${res.data.organization}/product-categories/${this.$route.params.id}`,{
            headers: {
              'Authorization': `Bearer ${localStorage.getItem('reg_token')}`,
              'Accept-Language': 'en'
            }
          })
              .then(res=>{
                this.title=res.data.title
              })
        })
  },
  methods:{

    trySubmitFile(e) {
      this.imageUrl=[]
      for (let i = 0; i < e.target.files.length; i++) {
        this.files_name.push(e.target.files[i])
      }
      var files = Array.from(e.target.files);
      console.log(typeof (files))
      files.forEach((file) => {
        this.myImages.push(file);
        var reader = new FileReader();
        var vm = this;
        reader.onload = function(e) {
          vm.imageUrl.push(e.target.result);
        };
        reader.readAsDataURL(file);
      });
    },
    create(){
      const form = new FormData();
      // form.append("image", this.myImages[0]);
      form.append("title", this.title);
      form.append("is_main", this.is_main);

      this.$axiosDefault.get(`me/`,
          {
            headers: {
              'Authorization': `Bearer ${localStorage.getItem('reg_token')}`,
              'Accept-Language':"oz"
            }
          }
      )
          .then(res=>{
            this.$axiosDefault.patch(`organizations/${res.data.organization}/product-categories/${this.$route.params.id}`,form,{
              headers: {
                'Authorization': `Bearer ${localStorage.getItem('reg_token')}`,
                'Accept-Language': 'oz'
              }
            })
                .then(el=>{
                  this.$toast("Malumot o'zbek tilida kiritildi", {
                    timeout: 2000,
                    type:"success"
                  });
                  this.$axiosDefault.patch(`organizations/${res.data.organization}/product-categories/${el.data.id}/`,{
                    "title":this.titleru,
                  },{
                    headers: {
                      'Authorization': `Bearer ${localStorage.getItem('reg_token')}`,
                      'Accept-Language': 'ru'
                    }
                  })
                      .then(()=>{

                        this.$toast("Malumot rus tilida kiritildi", {
                          timeout: 2000,
                          type:"success"
                        });
                      })
                  form.append("title", this.titleen);
                  this.$axiosDefault.patch(`organizations/${res.data.organization}/product-categories/${el.data.id}/`,{
                    "title":this.titleen,

                  },{
                    headers: {
                      'Authorization': `Bearer ${localStorage.getItem('reg_token')}`,
                      'Accept-Language': 'en'
                    }
                  })
                      .then(()=>{

                        this.$toast("Malumot ingliz tilida kiritildi", {
                          timeout: 2000,
                          type:"success"
                        });
                      })

                })
          })

          .catch(()=>{
            this.$toast("Rasm kiritish majburiy", {
              timeout: 2000,
              type:"error"
            });
          })
    }
  }

}
</script>
